.header {
  max-width: 880px;
  height: fit-content;
  margin: 0 auto 40px;
  padding: 45px 0 0;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header {
    max-width: 320px;
    margin-top: 28px;
    margin-bottom: 42px;
  }
}